html {
  box-sizing: border-box;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: "asd", sans-serif;
  overflow-x: hidden;
  will-change: scroll-position;
}

* {
  will-change: scroll-position;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

p {
